<template>
  <div class="con">
    <div v-for="(v,k) in info" class="type">
      <div class="title">{{v[0].title}}:</div>

      <div v-for="item in v">
        <div class="card">
          <div class="igb" :class="v[0].grid?'b0':''">
            <div :style="{backgroundImage:'url(' + item.url + ')'}"></div>
          </div>

          <span class="name">{{item.name}}</span>
          <span class="desc">{{item.desc}}</span>
        </div>

      </div>

    </div>
  </div>

</template>

<script>
import { car } from './type'

export default {
  name: 'pankou',
  data() {
    return {
      info: {
        car: car,
        sport_en: [
          { url: require('@/assets/img/adi.jpg'), name: '', desc: '', title: '' },
          { url: require('@/assets/img/puma.jpg'), name: '', desc: 'PUMA' },
          { url: require('@/assets/img/star.jpg'), name: '', desc: '' },
          { url: require('@/assets/img/nb.jpg'), name: '', desc: 'New Balance' },
          { url: require('@/assets/life/jack.jpg'), name: '', desc: '' },
        ],
        sport_zh: [
          { url: require('@/assets/img/hl.jpg'), name: '', desc: 'HLA', title: '' },
          { url: require('@/assets/img/peak.jpg'), name: '', desc: 'PEAK' },
          { url: require('@/assets/img/anta.jpg'), name: '', desc: 'ANTA' },
          { url: require('@/assets/img/tb.jpg'), name: '', desc: 'XTEP' },
          { url: require('@/assets/img/ning.jpg'), name: '', desc: 'LINING' },
          { url: require('@/assets/life/sm.jpg'), name: '', desc: 'Semir' },
          { url: require('@/assets/life/ts.jpg'), name: '', desc: 'TONLION' },
          { url: require('@/assets/life/yc.jpg'), name: '', desc: 'YISHION' },
        ],
        head: [
          { url: require('@/assets/life/hfs.jpg'), name: '', desc: '', title: '', grid: true },
          { url: require('@/assets/life/pr.jpg'), name: '', desc: '' },
          { url: require('@/assets/life/pt.jpg'), name: '', desc: '' },
          { url: require('@/assets/life/df.jpg'), name: '', desc: '' },
          { url: require('@/assets/life/sx.jpg'), name: '', desc: 'VS' },
          { url: require('@/assets/life/qy.jpg'), name: '', desc: '' },
          { url: require('@/assets/life/shk.jpg'), name: '', desc: '' },
          { url: require('@/assets/life/sl.jpg'), name: '', desc: 'SLEK' },
          { url: require('@/assets/life/sft.jpg'), name: '', desc: 'SOFTTO' },
        ],
        tooth: [
          { url: require('@/assets/life/jjs.jpg'), name: '', desc: 'Crest', title: '', grid: true },
          { url: require('@/assets/life/glj.jpg'), name: '', desc: 'Colgate' },
          { url: require('@/assets/life/hr.jpg'), name: '', desc: 'DARLIE' },
          { url: require('@/assets/life/lsl.jpg'), name: '', desc: '' },
        ],
        body: [
          { url: require('@/assets/life/sfj.jpg'), name: '', desc: '', title: '', grid: true },
          { url: require('@/assets/life/ls.jpg'), name: '', desc: '' },
        ],
      }
    }
  },
  methods: {

  },
  mounted() {

  }
}
</script>

<style lang="" scoped>
.con {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.type {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 40px;
  justify-content: center;
  position: relative;
  /* padding-left: 140px; */
}

.title {
  padding-right: 20px;
  width: 140px;
  position: absolute;
  left: 20px;
  top: -30px;
}

.card {
  width: 145px;
  height: 145px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  margin-bottom: 10px;
  border: 1px solid #cdcdcd;
  padding: 10px;
  border-radius: 10px;
}

.name {
  padding: 2px;
}

.desc {
  color: #999;
  font-size: 14px;
  white-space: pre-wrap;
}

.igb > div {
  width: 60px;
  height: 60px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}

.igb {
  border-radius: 50%;
  overflow: hidden;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.b0 {
  border-radius: 0;
}
</style>


