<template>
  <div class="container">
    <Input v-model="a" :rows='10' type="textarea" placeholder="" class="paiban-inp" @on-enter="deal" @on-blur="deal" />
    <div class="btnbox">
      <Button type="primary" @click="deal"></Button>
      <Button @click="reset"></Button>
    </div>
  </div>
</template>

<script>
export default {
  name: '',
  data() {
    return {
      a: ''
    }
  },
  methods: {
    reset() {
      this.a = ''
    },

    deal() {
      // [! ! ]
      this.a = this.a.replace(//g, '\n')
        .replace(/!/g, '\n')
        .replace(//g, '\n')
        .replace(//g, '\n')
        .replace(//g, '\n')
        .replace(/\?/g, '\n')
        .replace(//g, '\n')
        .replace(//g, '\n')
        .replace(//g, '\n')

    },

  }
}
</script>

<style lang="" scoped>
.container {
  width: 60%;
  margin: 0 auto;
}

.btnbox {
  margin: 20px;
}
</style>


