<template>
  <div>
    <div>f = (bp- q) / b</div>
    <br />
    <Button style="margin-bottom:20px" type="info" ghost @click="desc = !desc"></Button>
    <Button style="margin-bottom:20px;margin-left:40px" type="info" ghost @click="empt"></Button>
    <div v-if="desc" style="margin-bottom:20px">
      <div>f</div>
      <div>b</div>
      <div>p</div>
      <div>q</div>
      <br>
      <div>f0</div>
      <div>f</div>
      <div>f</div>
    </div>

    <div class="inp">
      <Input placeholder="" style="width:200px" v-model="b1"></Input>
      <Input placeholder="" style="width:200px;margin:0 20px" v-model="b2"></Input>
      <Input placeholder="" style="width:200px" v-model="b3" @keyup.enter.native="resu"></Input>
      <Button type="info" ghost style="margin-left:20px" @click="resu"></Button>
    </div>

    <div style="margin-top:20px" v-if="flag">
      f = {{msg}}

      <div style="margin:20px 0" v-if="msg>0">
        <Input placeholder="" style="width:200px" v-model="b4" @keyup.enter.native="end"></Input>
        <Button style="margin-left:40px" type="info" ghost @click="end"></Button>
        <div style="margin-top:20px">money = {{msg2}}</div>
        <br>
        <div style="color:green">(  = {{msg3}} )</div>
      </div>

    </div>

  </div>
</template>

<script>
import mixin from "./te.js";
export default {
  mixins: [mixin],
  data() {
    return {
      desc: false,
      b1: "",
      b2: "",
      b3: "",
      msg: "",
      flag: false,
      b4: "",
      msg2: "",
      msg3: "",
    };
  },

  mounted() {},

  methods: {
    empt() {
      this.b1 = "";
      this.b2 = "";
      this.b3 = "";
      this.flag = false;
    },
    resu() {
      let b = this.b1;
      let p = this.b2;
      let q = this.b3;

      if (!b || !p || !q) return;

      let f = (b * p - q) / b;
      f = Number(f);
      let re = Math.floor(f * 100) / 100;
      this.msg = re;
      this.flag = true;
      this.b4 = "";
      this.msg2 = "";
      this.msg3 = "";
    },
    end() {
      this.msg2 = this.msg * this.b4;
      this.msg3 = this.msg2 * this.b1;
    },
  },
};
</script>
<style scoped>
.inp {
  display: flex;
  justify-content: center;
}
</style>


